
@font-face {
  font-family: 'icomoon';
  src: url("../../common/fonts/icomoon.eot?drdffd");
  src: url("../../common/fonts/icomoon.eot?drdffd#iefix") format('embedded-opentype'), url("../../common/fonts/icomoon.ttf?drdffd") format('truetype'), url("../../common/fonts/icomoon.woff?drdffd") format('woff'), url("../../common/fonts/icomoon.svg?drdffd#icomoon") format('svg');
  font-weight: normal;
  font-style: normal;
}
[class^="icon-"],
[class*=" icon-"] {
/* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
/* Enable Ligatures ================ */
  letter-spacing: 0;
  -ms-font-feature-settings: "liga" 1;
  font-feature-settings: "liga";
  font-variant-ligatures: discretionary-ligatures;
/* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.icon-cart:before {
  content: "\e93a";
}
.icon-smile:before {
  content: "\e9e1";
}
.icon-sad:before {
  content: "\e9e5";
}
.icon-plus:before {
  content: "\ea0a";
}
.icon-minus:before {
  content: "\ea0b";
}
.icon-cancel-circle:before {
  content: "\ea0d";
}
.icon-arrow-right2:before {
  content: "\ea3c";
}
.icon-arrow-left2:before {
  content: "\ea40";
}
.icon-circle-left:before {
  content: "\ea44";
}
.icon-heart:before {
  content: "\e9da";
}
.food {
  position: fixed;
  bottom: 48px;
  left: 0;
  top: 0;
  background: #fff;
  z-index: 30;
  width: 100%;
  overflow: hidden;
  transition: all 0.5s linear;
/*构建一个正方形的容器*/
}
.food.rslide-enter,
.food.rslide-leave-active {
  transform: translate3D(100%, 0, 0);
}
.food.rslide-enter-active,
.food.rslide-leave {
  transform: translate3D(0, 0, 0);
}
.food .img-container {
  position: relative;
  width: 100%;
/*padding-top的百分比参照自身width*/
  padding-top: 100%;
  height: 0;
}
.food .img-container .banner {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}
.food .img-container .back {
  position: absolute;
  left: 10px;
  top: 10px;
}
.food .img-container .back .icon-circle-left {
  font-size: 20px;
  color: #fff;
}
.food .food-desc {
  padding: 18px;
  position: relative;
}
.food .food-desc .title {
  font-size: 14px;
  font-weight: 700;
  color: #07111b;
  line-height: 14px;
}
.food .food-desc .extra {
  margin-top: 8px;
  font-size: 0;
}
.food .food-desc .extra .sell,
.food .food-desc .extra .rating {
  font-size: 10px;
  color: #93999f;
  line-height: 10px;
  margin-right: 12px;
}
.food .food-desc .extra .price {
  margin-top: 18px;
}
.food .food-desc .nowprice {
  margin-right: 8px;
  font-weight: bold;
  color: #f01414;
  font-size: 14px;
}
.food .food-desc .oldprice {
  font-size: 10px;
  color: #93999f;
  font-weight: bold;
  line-height: 24px;
  text-decoration: line-through;
}
.food .cartcontrol-wrapper,
.food .addbtn {
  position: absolute;
  right: 18px;
  bottom: 18px;
}
.food .addbtn {
  padding: 6px 12px;
  background-color: #00a0dc;
  border-radius: 12px;
}
.food .addbtn span {
  font-size: 10px;
  color: #fff;
}
.food-info {
  padding: 18px;
}
.food-info .title {
  font-size: 14px;
  font-weight: 500;
  color: #07111b;
  line-height: 14px;
}
.food-info .text {
  margin-top: 6px;
  font-size: 12px;
  color: #93999f;
  line-height: 24px;
  font-weight: 200;
  padding: 0 8px;
}
.food-ratings .title {
  font-size: 14px;
  padding: 18px;
  font-weight: 500;
  color: #07111b;
  line-height: 14px;
}
.food-ratings .rating-content {
  margin: 0 18px;
}
.food-ratings .rating-content .rating-item {
  padding: 16px 0;
  position: relative;
}
.food-ratings .rating-content .rating-item:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: '';
}
.food-ratings .rating-content .rating-item .title {
  font-size: 0;
  padding: 0;
  overflow: hidden;
}
.food-ratings .rating-content .rating-item .title .rate-time {
  font-size: 10px;
  color: #93999f;
  line-height: 10px;
  float: left;
}
.food-ratings .rating-content .rating-item .title .rate-user {
  float: right;
  font-size: 0;
}
.food-ratings .rating-content .rating-item .title .rate-user .username {
  font-size: 10px;
  color: #93999f;
  line-height: 10px;
}
.food-ratings .rating-content .rating-item .title .rate-user .avatar {
  margin-left: 6px;
}
.food-ratings .rating-content .rating-item .text {
  font-size: 12px;
  line-height: 24px;
  color: #07111b;
}
.food-ratings .rating-content .rating-item .text .icon {
  font-size: 12px;
  color: #93999f;
  line-height: 24px;
  margin-right: 4px;
}
.food-ratings .rating-content .rating-item .text .icon.icon-smile {
  color: #00a0dc;
}
.food-ratings .rating-content .rating-item .text .icon.icon-sad {
  color: #93999f;
}


