#seller {
  position: absolute;
  top: 185px;
  width: 100%;
  bottom: 18px;
/*overflow:auto*/
  overflow: hidden;
}
#seller .info {
  position: relative;
}
#seller .info .title {
  margin: 18px;
  margin-bottom: 0;
  font-size: 14px;
  color: #07111b;
  line-height: 14px;
}
#seller .info .extra {
  margin: 8px 18px 0;
  padding-bottom: 18px;
  position: relative;
  font-size: 0;
}
#seller .info .extra:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: '';
}
#seller .info .extra .star {
  display: inline-block;
}
#seller .info .extra .rating-count {
  margin: 0 12px 0 8px;
  font-size: 10px;
  color: #4d555d;
  line-height: 10px;
}
#seller .info .extra .sell-count {
  font-size: 10px;
  color: #4d555d;
  line-height: 10px;
}
#seller .info .collect {
  position: absolute;
  right: 18px;
  top: 0;
  text-align: center;
}
#seller .info .collect .icon-heart {
  font-size: 24px;
  display: block;
  color: #808080;
  width: 36px;
  margin-bottom: 4px;
}
#seller .info .collect .icon-heart.active {
  color: #f00;
}
#seller .info .collect .text {
  font-size: 10px;
  color: #4d555d;
  line-height: 10px;
}
#seller .info .desc {
  display: -ms-flexbox;
  display: flex;
}
#seller .info .desc .block {
  -ms-flex: 1;
      flex: 1;
  text-align: center;
  margin-top: 18px;
  margin-bottom: 18px;
  border-right: 1px solid rgba(7,17,27,0.1);
}
#seller .info .desc .block .name {
  font-size: 10px;
  color: #93999f;
  line-height: 10px;
  margin: 0;
  display: block;
}
#seller .info .desc .block .value {
  display: block;
  margin-top: 4px;
  line-height: 24px;
  font-size: 10px;
  font-weight: 200;
}
#seller .info .desc .block .value .num {
  font-size: 24px;
  font-weight: 200;
  color: #07111b;
  line-height: 24px;
}
