.ratingselect {
  background: #fff;
}
.ratingselect .type-wrapper {
  padding-bottom: 18px;
  margin: 0 18px;
  position: relative;
  font-size: 0;
}
.ratingselect .type-wrapper:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: '';
}
.ratingselect .type-wrapper .item-wrapper {
  display: inline-block;
  padding: 8px 12px;
  border-radius: 2px;
  margin-right: 8px;
}
.ratingselect .type-wrapper .item-wrapper.all {
  background: rgba(0,160,220,0.2);
}
.ratingselect .type-wrapper .item-wrapper.all.active {
  background: #00a0dc;
}
.ratingselect .type-wrapper .item-wrapper.positive {
  background: rgba(0,160,220,0.2);
}
.ratingselect .type-wrapper .item-wrapper.positive.active {
  background: #00a0dc;
}
.ratingselect .type-wrapper .item-wrapper.negative {
  background: rgba(77,85,93,0.2);
}
.ratingselect .type-wrapper .item-wrapper.negative.active {
  background: rgba(77,85,93,0.5);
}
.ratingselect .type-wrapper .item-wrapper.active span {
  color: #fff;
}
.ratingselect .type-wrapper .item-wrapper .big {
  color: #4d555d;
  font-size: 14px;
  font-weight: 500;
  margin-right: 3px;
  line-height: 14px;
}
.ratingselect .type-wrapper .item-wrapper .num {
  color: #4d555d;
  font-size: 12px;
}
.ratingselect .switch-wrapper {
  width: 100%;
  position: relative;
  padding: 18px;
  font-size: 0;
}
.ratingselect .switch-wrapper:after {
  display: block;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: '';
}
.ratingselect .switch-wrapper .gou {
  display: inline-block;
  width: 14px;
  height: 14px;
  background: #93999f;
  border-radius: 50%;
  margin-right: 4px;
  vertical-align: top;
  color: #fff;
  font-size: 12px;
  text-align: center;
}
.ratingselect .switch-wrapper .gou.on {
  background: #00c850;
}
.ratingselect .switch-wrapper .text {
  font-size: 12px;
  color: #93999f;
  line-height: 12px;
}


