
#cart-control {
  position: relative;
/*最终和最开始的样式，先有icon-minus,后有.scroll-enter,然后是scroll-enter-active,最后又回归icon-minus*/
}
#cart-control .minus,
#cart-control .plus {
  display: inline-block;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  font-size: 12px;
  text-align: center;
  line-height: 18px;
}
#cart-control .icon-minus {
  position: absolute;
  right: 54px;
  top: 0;
  color: #00a0dc;
  border: 2px solid #00a0dc;
  transition: all 0.5s;
}
#cart-control .icon-minus.scroll-enter-active,
#cart-control .icon-minus.scroll-leave {
  opacity: 1;
  transform: translate3D(0, 0, 0) rotate(0deg);
}
#cart-control .icon-minus.scroll-enter,
#cart-control .icon-minus.scroll-leave-active {
  opacity: 0;
  transform: translate3D(54px, 0, 0) rotate(360deg);
}
#cart-control .icon-plus {
  color: #fff;
  background-color: #00a0dc;
  width: 22px;
  height: 22px;
  line-height: 22px;
}
#cart-control .num {
  font-size: 12px;
  color: #93999f;
  margin-left: 8px;
  margin-right: 8px;
}


